"use client";

import * as React from "react";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { MapPin } from "lucide-react";

const CityToggle = () => {
  return (
    <Select defaultValue="paris">
      <SelectTrigger className="w-auto flex items-center">
        <MapPin className="mr-2" />
      </SelectTrigger>
      <SelectContent>
        <SelectItem value="marseille">Marseille</SelectItem>
        <SelectItem value="paris">Paris</SelectItem>
        <SelectItem value="rennes">Rennes</SelectItem>
      </SelectContent>
    </Select>
  );
};

export default CityToggle;

"use client"

import { Button } from "@/components/ui/button"
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"

const CityToggle: React.FC = () => {
  const { setCity } = useTheme()

  return (
    <DropdownMenu>
      <DropdownMenuTrigger asChild>
        <Button variant="outline" size="sm">
          <MapPin className="h-[1.2rem] w-[1.2rem] rotate-0 scale-100 transition-all dark:-rotate-90 dark:scale-0" />
          <span className="sr-only">Toggle city</span>
        </Button>
      </DropdownMenuTrigger>
      <DropdownMenuContent align="end">
        <DropdownMenuItem className="space-x-2" onClick={() => setCity("marseille")}>
          <span>Marseille</span>
        </DropdownMenuItem>
        <DropdownMenuItem className="space-x-2" onClick={() => setCity("paris")}>
          <span>Paris</span>
        </DropdownMenuItem>
        <DropdownMenuItem className="space-x-2" onClick={() => setCity("rennes")}>
          <span>Rennes</span>
        </DropdownMenuItem>
      </DropdownMenuContent>
    </DropdownMenu>
  )
}

export default CityToggle;
